import React, { createContext, useState, useEffect } from 'react';

export const AuthContext = createContext();

export const AuthContextProvider = ({ children }) => {

    const setLoginData = (data) => {
        localStorage.setItem('userData', JSON.stringify({
            status: true,
            token: data.token,
            user: data.user
        })); // Save the user object in local storage
        setUserInfo(JSON.parse(data.user)[0]);
        setUserData({
            status: true,
            token: data.token,
            user: data.user
        });
    };

    const stateData = {
        status: false,
        token: null,
        user: null
    }; // initial user state date

    const logoutUser = () => {
        localStorage.removeItem('userData');
        setUserData({});
        setUserInfo({});
    };

    const sessionData = JSON.parse(localStorage.getItem('userData')) || {};
    let [userData, setUserData] = useState(sessionData);

    const sessionUserInfo = sessionData && sessionData.user && JSON.parse(sessionData.user)[0];
    let [userInfo, setUserInfo] = useState(sessionUserInfo);

    return (
        <AuthContext.Provider value={{ userData, userInfo, setLoginData, logoutUser }}>
            {children}
        </AuthContext.Provider>
    );
};
