import React, { useState, useContext } from 'react';
import { BrowserRouter as Router, Switch, Redirect } from 'react-router-dom';
import Header from './components/Header';
import Home from './pages/Home';
import { AuthContext } from "./helpers/AuthContext";
import ProtectedRoute  from "./helpers/ProtectedRoute";
import PublicRoute  from "./helpers/PublicRoute";

const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse">Loading...</div>
  </div>
)

const Login = React.lazy(() => import('./pages/Login'));
const Dashboard = React.lazy(() => import('./pages/Dashboard'));
const ScheduledList = React.lazy(() => import('./pages/ScheduledList'));
const InspectedList = React.lazy(() => import('./pages/InspectedList'));
const ReScheduledList = React.lazy(() => import('./pages/ReScheduledList'));
const InspectionReport = React.lazy(() => import('./pages/InspectionReport'));
const ScheduleSiteVisit = React.lazy(() => import('./pages/ScheduleSiteVisit'));
const LoadForInspection = React.lazy(() => import('./pages/LoadForInspection'));
const NotFound = React.lazy(() => import('./pages/NotFound'));

function App() {

  const { userData } = useContext(AuthContext);

  return (
    <div className="wrapper">
        <Router>
          <React.Suspense fallback={loading}>
            {userData.status ? <Header /> : ''}
            <Switch>
                <PublicRoute path="/login" exact component={Login} />
                <ProtectedRoute path="/" exact component={Dashboard} />
                <ProtectedRoute path="/dashboard" exact component={Dashboard} />
                <ProtectedRoute path="/scheduledList" exact component={ScheduledList} />
                <ProtectedRoute path="/inspectedList" exact component={InspectedList} />
                <ProtectedRoute path="/rescheduleVisit" exact component={ReScheduledList} />
                <ProtectedRoute path="/inspectionReport" exact component={InspectionReport} />
                <ProtectedRoute path="/scheduleSiteVisit/:unitId/:Re?"  component={ScheduleSiteVisit} />
                <ProtectedRoute path="/loadForInspection/:unitId/:ucId"  component={LoadForInspection} />
                <ProtectedRoute path="/inspectionReport/:unitId/:ucId"  component={InspectionReport} />
                <ProtectedRoute path="/*" exact component={NotFound} />
                <Redirect from="/" to="/dashboard" />
            </Switch>
          </React.Suspense>
        </Router>
    </div>
  );
}

export default App;
