import React, { useContext, useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import Nav from './Nav';
import logo from '../images/RAK-Logo-Small-White.png'
import avatar from '../images/default-avatar.png'
import { AuthContext } from "../helpers/AuthContext";

export default function Header() {
    const { userInfo, logoutUser } = useContext(AuthContext);
    const [menuClass, setMenuClass] = useState('nav-bottom');
    const history = useHistory();
    const logout = e => {
      logoutUser();
      history.push("/login");
    }
    const toggleMenu = function() {
      if ( menuClass.indexOf('header-toggled') === -1) {
        setMenuClass('nav-bottom header-toggled');
      } else {
        setMenuClass('nav-bottom');
      }
    }

    return (
      <nav className="navbar horizontal-layout col-lg-12 col-12 p-0">
        <div className="nav-top flex-grow-1">
          <div className="container d-flex flex-row h-100 align-items-center">
            <div className="text-center navbar-brand-wrapper d-flex align-items-center">
              <Link className="navbar-brand brand-logo" to="/"><img src={logo} alt="logo" /></Link>
              <Link className="navbar-brand brand-logo-mini" to="/"><img src={logo} alt="logo" /></Link>
            </div>
            <div className="navbar-menu-wrapper d-flex align-items-center justify-content-between flex-grow-1">
              <form className="search-field d-none  d-none"  action="#">
                <div className="form-group mb-0">
                  <div className="input-group">
                    <div className="input-group-prepend">
                      <span className="input-group-text"><i className="icon-magnifier" /></span>
                    </div>
                    <input type="text" className="form-control" placeholder="search here..." />
                  </div>
                </div>
              </form>
              <ul className="navbar-nav navbar-nav-right mr-0 ml-auto">

                <li className="nav-item nav-profile dropdown">
                  <Link className="nav-link dropdown-toggle" href="index.html#" data-toggle="dropdown" id="profileDropdown">
                    <img src={avatar} alt="profile" />
                    <span className="nav-profile-name">{userInfo.AdminUser} ({userInfo.AdminType})</span>
                  </Link>
                  <div className="dropdown-menu dropdown-menu-right navbar-dropdown" aria-labelledby="profileDropdown">
                    {/* <Link className="dropdown-item d-none">
                      <i className="icon-settings text-primary mr-2" />
                      Settings
                    </Link> */}
                    <div className="dropdown-divider" />
                    <Link className="dropdown-item" to="/" onClick={logout}>
                      <i className="icon-logout text-primary mr-2" />
                      Logout
                    </Link>
                  </div>
                </li>
              </ul>
              <button className="navbar-toggler align-self-center" type="button" data-toggle="minimize">
                <span onClick={toggleMenu} className="icon-menu" />
              </button>
            </div>
          </div>
        </div>
        <div className={menuClass}>
          <div className="container">
            <Nav handleClick={toggleMenu} />
          </div>
        </div>
      </nav>
    );
}
